<template>
    <main class="container mx-auto px-4 py-8 dark:bg-neutral-900 rounded-2xl flex flex-col min-h-[70vh]">
        <!-- 标题区域 -->
        <div class="mb-8 flex items-center bg-gradient-to-r from-primary/10 to-transparent p-4 rounded-lg">
            <div class="h-14 w-14 flex items-center justify-center bg-primary rounded-full shadow-lg mr-4">
                <i class="fa fa-exchange text-3xl text-white"></i>
            </div>
            <div>
                <h1 class="text-2xl md:text-3xl font-bold text-primary dark:text-primary-400 mb-1">CSS 转 Tailwind 实时预览</h1>
                <p class="text-neutral-600 dark:text-neutral-300 text-base">
                    输入 CSS 片段，自动转换为 Tailwind 类并实时预览效果。支持历史记录和双向转换。
                </p>
            </div>
        </div>
        
        <!-- 主内容区域 -->
        <div class="grid grid-cols-1 lg:grid-cols-12 gap-6 flex-1">
            <!-- 左侧面板 -->
            <div class="lg:col-span-5 flex flex-col">
                <!-- CSS输入组件 -->
                <css-input 
                    :cssInput="cssInput"
                    :tailwindInput="tailwindInput"
                    :cssFromTailwind="cssFromTailwind"
                    @update:cssInput="updateCssInput"
                    @update:tailwindInput="tailwindInput = $event"
                    @update:cssFromTailwind="cssFromTailwind = $event"
                    @convert-tailwind="tailwindToCss"
                    @save-history="saveToHistory"
                />
                
                <!-- 历史记录面板 -->
                <div class="bg-white dark:bg-neutral-800 rounded-xl shadow-lg border border-neutral-100 dark:border-neutral-700 p-4 mt-6 animate-fade-in">
                    <div 
                        @click="isHistoryExpanded = !isHistoryExpanded" 
                        class="flex items-center justify-between p-2 rounded-lg cursor-pointer transition-colors duration-200 active:opacity-80"
                        :class="isHistoryExpanded 
                            ? 'bg-neutral-100 dark:bg-neutral-700' 
                            : 'bg-neutral-50 dark:bg-neutral-800 hover:bg-neutral-100 dark:hover:bg-neutral-750'"
                    >
                        <div class="flex items-center">
                            <i class="fa fa-history text-primary dark:text-primary-400 mr-2"></i>
                            <span class="font-medium">历史记录</span>
                            <span class="ml-2 text-xs bg-neutral-200 dark:bg-neutral-600 text-neutral-700 dark:text-neutral-300 rounded-full px-2">
                                {{ history.length }}
                            </span>
                        </div>
                        <i :class="`fa fa-chevron-${isHistoryExpanded ? 'up' : 'down'} text-sm text-neutral-500 transition-transform duration-300`"></i>
                    </div>
                    
                    <div v-show="isHistoryExpanded" class="mt-2 animate-slide-down">
                        <div v-if="history.length === 0" class="text-center py-4 text-neutral-500 dark:text-neutral-400">
                            <i class="fa fa-info-circle mr-1"></i> 暂无历史记录
                        </div>
                        <div v-else class="max-h-64 overflow-y-auto scrollbar-hide">
                            <div 
                                v-for="(item, index) in history" 
                                :key="index" 
                                class="p-2 border-b border-neutral-100 dark:border-neutral-700 last:border-0 hover:bg-neutral-50 dark:hover:bg-neutral-750 cursor-pointer transition-all duration-200 group"
                                @click="loadFromHistory(item)"
                            >
                                <div class="flex items-center justify-between">
                                    <div class="text-sm font-medium text-neutral-700 dark:text-neutral-300 truncate flex-1">
                                        {{ item.name || `记录 ${index + 1}` }}
                                    </div>
                                    <div class="flex items-center">
                                        <span class="text-xs text-neutral-500 dark:text-neutral-400 mr-2">
                                            {{ formatDate(item.timestamp) }}
                                        </span>
                                        <button 
                                            @click.stop="removeHistoryItem(index)" 
                                            class="text-neutral-400 hover:text-red-500 dark:text-neutral-500 dark:hover:text-red-400 opacity-0 group-hover:opacity-100 transition-opacity"
                                        >
                                            <i class="fa fa-times"></i>
                                        </button>
                                    </div>
                                </div>
                                <div class="text-xs text-neutral-500 dark:text-neutral-400 mt-1 truncate">
                                    {{ item.cssInput.substring(0, 50) + (item.cssInput.length > 50 ? '...' : '') }}
                                </div>
                            </div>
                        </div>
                        <div v-if="history.length > 0" class="flex justify-end mt-2">
                            <button 
                                @click="clearHistory" 
                                class="text-xs px-3 py-1 rounded bg-red-50 dark:bg-red-900/30 text-red-500 dark:text-red-400 hover:bg-red-100 dark:hover:bg-red-900/50 transition-all"
                            >
                                <i class="fa fa-trash-o mr-1"></i> 清空历史
                            </button>
                        </div>
                    </div>
                </div>
            </div>
            
            <!-- 右侧预览面板 -->
            <div class="lg:col-span-7">
                <tailwind-preview 
                    :tailwindClassList="tailwindClassList" 
                    :customText="previewText"
                    @update:customText="previewText = $event"
                />
            </div>
        </div>
        
        <!-- 底部提示区域 -->
        <div class="mt-8 bg-blue-50 dark:bg-blue-900/20 border border-blue-100 dark:border-blue-800 rounded-lg p-4 text-blue-800 dark:text-blue-300 text-sm">
            <div class="flex items-start">
                <i class="fa fa-lightbulb-o text-xl mr-3 mt-0.5"></i>
                <div>
                    <div class="font-medium mb-1">功能提示</div>
                    <ul class="list-disc pl-5 space-y-1">
                        <li>支持 CSS → Tailwind 和 Tailwind → CSS 双向转换</li>
                        <li>可以单独启用或禁用生成的 Tailwind 类，查看效果变化</li>
                        <li>支持保存样式到历史记录，方便后续使用</li>
                        <li>提供多种预览模式：文本、按钮、卡片和标签</li>
                        <li>支持自定义预览文本内容</li>
                        <li>现在支持更多 Tailwind 类的反向转换，包括布局、尺寸、文字、边框、过渡等</li>
                    </ul>
                </div>
            </div>
        </div>
        
        <!-- Toast提示组件 -->
        <transition name="toast">
            <div 
                v-if="showToast" 
                class="fixed bottom-6 right-6 px-4 py-3 rounded-lg shadow-lg flex items-center z-50"
                :class="{
                    'bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-300': toastType === 'success',
                    'bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300': toastType === 'info',
                    'bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-300': toastType === 'error'
                }"
            >
                <i 
                    class="mr-2 text-lg"
                    :class="{
                        'fa fa-check-circle': toastType === 'success',
                        'fa fa-info-circle': toastType === 'info',
                        'fa fa-exclamation-circle': toastType === 'error'
                    }"
                ></i>
                {{ toastMessage }}
            </div>
        </transition>
    </main>
</template>

<script>
import { css2tw, tailwind2css, defaultSample } from '@/config/cssToTailwind';
import CssInput from '@/components/css2tailwind/CssInput.vue';
import TailwindPreview from '@/components/css2tailwind/TailwindPreview.vue';

export default {
    name: 'Css2TailwindPage',
    components: {
        CssInput,
        TailwindPreview
    },
    data() {
        return {
            cssInput: '',
            tailwindInput: '',
            cssFromTailwind: '',
            history: [],
            isHistoryExpanded: false,
            previewText: 'Tailwind 预览文本',
            localStorageKey: 'css2tailwind_history',
            showToast: false,
            toastMessage: '',
            toastType: 'info', // 'success', 'info', 'error'
            toastTimeout: null
        };
    },
    computed: {
        tailwindClassList() {
            return css2tw(this.cssInput);
        }
    },
    methods: {
        tailwindToCss() {
            if (!this.tailwindInput.trim()) {
                this.cssFromTailwind = '';
                return;
            }
            
            // 转换Tailwind类为CSS
            const result = tailwind2css(this.tailwindInput);
            
            // 检查是否有未识别的类
            const unrecognizedCount = (result.match(/\/\* 未识别:/g) || []).length;
            
            // 设置转换结果
            this.cssFromTailwind = result;
            
            // 显示提示信息
            if (unrecognizedCount > 0) {
                this.showToastMessage(`转换完成，但有 ${unrecognizedCount} 个类未能识别`, 'info');
            } else {
                this.showToastMessage('转换成功！', 'success');
            }
        },
        updateCssInput(value) {
            this.cssInput = value;
        },
        saveToHistory(name = '') {
            if (!this.cssInput.trim()) return;
            
            // 创建历史记录项
            const historyItem = {
                cssInput: this.cssInput,
                tailwindInput: this.tailwindInput,
                cssFromTailwind: this.cssFromTailwind,
                name: name || `样式 ${this.history.length + 1}`,
                timestamp: Date.now()
            };
            
            // 添加到历史记录
            this.history.unshift(historyItem);
            
            // 限制历史记录数量
            if (this.history.length > 20) {
                this.history.pop();
            }
            
            // 保存到本地存储
            this.saveHistoryToLocalStorage();
            
            // 显示历史记录面板
            this.isHistoryExpanded = true;
            
            // 显示提示
            this.showToastMessage('样式已保存到历史记录', 'success');
        },
        loadFromHistory(item) {
            this.cssInput = item.cssInput;
            this.tailwindInput = item.tailwindInput || '';
            this.cssFromTailwind = item.cssFromTailwind || '';
            
            // 显示提示
            this.showToastMessage('已加载历史样式', 'success');
        },
        removeHistoryItem(index) {
            this.history.splice(index, 1);
            this.saveHistoryToLocalStorage();
            
            // 显示提示
            this.showToastMessage('已删除历史记录', 'info');
        },
        clearHistory() {
            if (confirm('确定要清空所有历史记录吗？')) {
                this.history = [];
                this.saveHistoryToLocalStorage();
                
                // 显示提示
                this.showToastMessage('已清空所有历史记录', 'info');
            }
        },
        formatDate(timestamp) {
            const date = new Date(timestamp);
            return `${date.getMonth() + 1}/${date.getDate()} ${date.getHours()}:${String(date.getMinutes()).padStart(2, '0')}`;
        },
        saveHistoryToLocalStorage() {
            try {
                localStorage.setItem(this.localStorageKey, JSON.stringify(this.history));
            } catch (e) {
                console.error('无法保存历史记录到本地存储:', e);
                this.showToastMessage('无法保存到本地存储', 'error');
            }
        },
        loadHistoryFromLocalStorage() {
            try {
                const savedHistory = localStorage.getItem(this.localStorageKey);
                if (savedHistory) {
                    this.history = JSON.parse(savedHistory);
                }
            } catch (e) {
                console.error('无法从本地存储加载历史记录:', e);
                this.showToastMessage('无法加载历史记录', 'error');
            }
        },
        showToastMessage(message, type = 'info') {
            // 清除之前的定时器
            if (this.toastTimeout) {
                clearTimeout(this.toastTimeout);
            }
            
            // 设置新的提示
            this.toastMessage = message;
            this.toastType = type;
            this.showToast = true;
            
            // 设置自动隐藏
            this.toastTimeout = setTimeout(() => {
                this.showToast = false;
            }, 3000);
        }
    },
    mounted() {
        // 加载历史记录
        this.loadHistoryFromLocalStorage();
        
        // 初始化时填充样例
        this.$nextTick(() => {
            if (!this.cssInput) {
                this.cssInput = defaultSample;
            }
        });
    },
    beforeDestroy() {
        // 清除定时器
        if (this.toastTimeout) {
            clearTimeout(this.toastTimeout);
        }
    }
};
</script>

<style scoped>
@keyframes slideDown {
    from { max-height: 0; opacity: 0; }
    to { max-height: 500px; opacity: 1; }
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

.animate-slide-down {
    animation: slideDown 0.3s ease-out;
    overflow: hidden;
}

.animate-fade-in {
    animation: fadeIn 0.3s ease-in-out;
}

.toast-enter-active, .toast-leave-active {
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.toast-enter, .toast-leave-to {
    transform: translateY(20px);
    opacity: 0;
}
</style>
