<template>
  <div class="bg-white dark:bg-neutral-800 rounded-xl shadow-lg border border-neutral-100 dark:border-neutral-700 p-4 md:p-6 flex flex-col">
    <div class="flex items-center mb-4">
      <h2 class="text-lg font-semibold text-primary dark:text-primary-400 flex items-center">
        <i class="fa fa-code mr-2"></i>CSS 输入
      </h2>
      <div class="ml-auto flex items-center gap-2">
        <button
          class="text-xs px-3 py-1 rounded bg-primary/10 dark:bg-primary-900 text-primary dark:text-primary-300 hover:bg-primary/20 transition-all flex items-center"
          @click="fillSample">
          <i class="fa fa-file-text-o mr-1"></i>
          填充样例
        </button>
        <button
          class="text-xs px-3 py-1 rounded bg-neutral-200 dark:bg-neutral-700 text-neutral-700 dark:text-neutral-200 hover:bg-neutral-300 dark:hover:bg-neutral-600 transition-all flex items-center"
          @click="clearInput">
          <i class="fa fa-eraser mr-1"></i>
          清空
        </button>
        <button
          class="text-xs px-3 py-1 rounded bg-secondary/10 dark:bg-secondary-900 text-secondary dark:text-secondary-300 hover:bg-secondary/20 transition-all flex items-center"
          @click="resetInput">
          <i class="fa fa-refresh mr-1"></i>
          重置
        </button>
      </div>
    </div>
    
    <div class="relative">
      <textarea 
        v-model="cssInputValue"
        class="w-full h-80 bg-neutral-50 dark:bg-neutral-900 border border-primary-100 dark:border-primary-800 rounded-lg p-3 font-mono text-base text-neutral-800 dark:text-neutral-100 focus:outline-none focus:ring-2 focus:ring-primary/30 resize-none transition-all"
        placeholder="如：color: #f00; font-size: 20px; padding: 8px 16px; border-radius: 8px;"
      ></textarea>
    </div>
    
    <!-- 操作按钮区域 -->
    <div class="flex items-center justify-between mt-3">
      <div class="flex items-center">
        <h3 class="text-sm font-medium text-neutral-700 dark:text-neutral-300 flex items-center">
          <i class="fa fa-exchange mr-1"></i>
          Tailwind → CSS 反向转换
        </h3>
        <div class="relative ml-2">
          <button 
            @click="showTwHelpTips = !showTwHelpTips"
            class="text-xs px-1.5 py-0.5 rounded-full bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300 hover:bg-blue-200 dark:hover:bg-blue-800 transition-all flex items-center"
          >
            <i class="fa fa-question"></i>
          </button>
          
          <div 
            v-if="showTwHelpTips" 
            class="absolute left-0 top-full mt-2 w-64 p-3 bg-white dark:bg-neutral-800 rounded-lg shadow-lg border border-neutral-200 dark:border-neutral-700 z-10 text-xs text-neutral-700 dark:text-neutral-300"
          >
            <div class="font-medium mb-1">支持的Tailwind类:</div>
            <ul class="list-disc pl-4 space-y-1">
              <li>布局类: flex, grid, block, hidden等</li>
              <li>尺寸类: w-full, h-screen, min-h-full等</li>
              <li>间距类: p-[10px], m-[1rem], gap-[5px]等</li>
              <li>文字类: text-center, font-bold, tracking-wide等</li>
              <li>边框类: border, rounded-full, border-dashed等</li>
              <li>背景/颜色类: bg-[#fff], text-[red]等</li>
              <li>过渡/变换类: transition, scale-110, rotate-45等</li>
              <li>位置类: static, absolute, z-10等</li>
              <li>更多详见文档</li>
            </ul>
          </div>
        </div>
      </div>
      
      <div class="flex items-center gap-2">
        <!-- 保存按钮移到这里 -->
        <button
          v-if="cssInputValue.trim()"
          @click="saveToHistory"
          class="text-xs px-3 py-1 rounded bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-300 hover:bg-green-200 dark:hover:bg-green-800 transition-all flex items-center"
          title="保存到历史记录"
        >
          <i class="fa fa-save mr-1"></i>
          保存样式
        </button>
      </div>
    </div>
    
    <div class="flex flex-col mt-2">
      <div class="flex items-center justify-between mb-2">
        <label class="text-sm font-medium text-neutral-700 dark:text-neutral-300 flex items-center">
          <i class="fa fa-code mr-1"></i>
          输入 Tailwind 类名
        </label>
        <div class="flex items-center gap-2">
          <button
            v-if="tailwindInputValue.trim()"
            @click="clearTailwindInput"
            class="text-xs px-2 py-0.5 rounded bg-neutral-200 dark:bg-neutral-700 text-neutral-700 dark:text-neutral-300 hover:bg-neutral-300 dark:hover:bg-neutral-600 transition-all"
          >
            <i class="fa fa-eraser"></i>
          </button>
          <button
            v-if="tailwindInputValue.trim()"
            @click="convertTailwindToCss"
            class="text-xs px-3 py-1 rounded bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300 hover:bg-blue-200 dark:hover:bg-blue-800 transition-all flex items-center"
          >
            <i class="fa fa-arrow-up mr-1"></i>
            转换为CSS
          </button>
        </div>
      </div>
      
      <textarea
        v-model="tailwindInputValue"
        class="w-full h-24 bg-neutral-50 dark:bg-neutral-900 border border-primary-100 dark:border-primary-800 rounded-lg p-3 font-mono text-base text-primary dark:text-primary-300 focus:outline-none focus:ring-2 focus:ring-primary/30 resize-none transition-all"
        placeholder="输入 Tailwind 类名（空格分隔），如: flex items-center p-4 text-center rounded-lg shadow-md"
      ></textarea>
    </div>
    
    <div 
      v-if="cssFromTailwind" 
      class="mt-3"
    >
      <div class="flex items-center justify-between mb-2">
        <h3 class="text-sm font-medium text-neutral-700 dark:text-neutral-300 flex items-center">
          <i class="fa fa-css3 mr-1"></i>
          转换结果
        </h3>
        <div class="flex items-center gap-2">
          <button
            @click="copyToClipboard(cssFromTailwind)"
            class="text-xs px-3 py-1 rounded bg-primary/10 dark:bg-primary-900 text-primary dark:text-primary-300 hover:bg-primary/20 transition-all flex items-center"
          >
            <i class="fa fa-copy mr-1"></i>
            复制CSS
          </button>
          <button
            @click="applyCssToInput"
            class="text-xs px-3 py-1 rounded bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-300 hover:bg-green-200 dark:hover:bg-green-800 transition-all flex items-center"
          >
            <i class="fa fa-arrow-up mr-1"></i>
            应用到输入框
          </button>
        </div>
      </div>
      
      <div class="text-xs text-neutral-700 dark:text-neutral-200 bg-neutral-100 dark:bg-neutral-800 rounded p-3 font-mono whitespace-pre-wrap break-all animate-fade-in">
        {{ cssFromTailwind }}
      </div>
    </div>
    
    <div v-if="copied" class="mt-2 text-xs text-green-600 dark:text-green-400 flex items-center justify-end animate-fade-in">
      <i class="fa fa-check-circle mr-1"></i>
      已复制到剪贴板！
    </div>
  </div>
</template>

<script>
import { defaultSample } from '@/config/cssToTailwind';

export default {
  name: 'CssInput',
  props: {
    cssInput: {
      type: String,
      required: true
    },
    tailwindInput: {
      type: String,
      default: ''
    },
    cssFromTailwind: {
      type: String,
      default: ''
    }
  },
  emits: ['update:cssInput', 'update:tailwindInput', 'update:cssFromTailwind', 'convert-tailwind', 'save-history'],
  data() {
    return {
      showTwHelpTips: false,
      copied: false
    };
  },
  computed: {
    cssInputValue: {
      get() {
        return this.cssInput;
      },
      set(value) {
        this.$emit('update:cssInput', value);
      }
    },
    tailwindInputValue: {
      get() {
        return this.tailwindInput;
      },
      set(value) {
        this.$emit('update:tailwindInput', value);
      }
    }
  },
  methods: {
    fillSample() {
      this.$emit('update:cssInput', defaultSample);
    },
    clearInput() {
      this.$emit('update:cssInput', '');
      this.$emit('update:tailwindInput', '');
      this.$emit('update:cssFromTailwind', '');
    },
    clearTailwindInput() {
      this.$emit('update:tailwindInput', '');
      this.$emit('update:cssFromTailwind', '');
    },
    resetInput() {
      this.$emit('update:cssInput', defaultSample);
    },
    saveToHistory() {
      if (!this.cssInputValue.trim()) return;
      
      // 提示用户输入名称
      const name = prompt('请为此样式命名（可选）:', '');
      this.$emit('save-history', name);
    },
    convertTailwindToCss() {
      if (!this.tailwindInputValue.trim()) return;
      this.$emit('update:cssFromTailwind', '正在转换...');
      // 使用父组件的转换方法
      this.$emit('convert-tailwind');
    },
    copyToClipboard(text) {
      if (!text) return;
      
      navigator.clipboard.writeText(text).then(() => {
        this.copied = true;
        setTimeout(() => { this.copied = false; }, 2000);
      });
    },
    applyCssToInput() {
      if (!this.cssFromTailwind) return;
      
      // 将转换结果应用到CSS输入框
      this.$emit('update:cssInput', this.cssFromTailwind);
    }
  }
};
</script>

<style scoped>
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.animate-fade-in {
  animation: fadeIn 0.3s ease-in-out;
}
</style> 