<template>
  <div class="bg-white dark:bg-neutral-800 rounded-xl shadow-lg border border-neutral-100 dark:border-neutral-700 p-4 md:p-6 flex flex-col h-full relative">
    <div class="flex items-center mb-4">
      <h2 class="text-lg font-semibold text-primary dark:text-primary-400 flex items-center">
        <i class="fa fa-magic mr-2"></i>Tailwind 类 & 实时预览
      </h2>
      <div class="ml-auto flex items-center gap-2">
        <button
          class="text-xs px-3 py-1 rounded bg-primary/10 dark:bg-primary-900 text-primary dark:text-primary-300 hover:bg-primary/20 transition-all flex items-center"
          @click="copyTailwind"
          :disabled="!tailwindClass"
          :class="{'opacity-50 cursor-not-allowed': !tailwindClass}"
        >
          <i class="fa fa-copy mr-1"></i>
          复制类名
        </button>
        <button
          class="text-xs px-3 py-1 rounded bg-secondary/10 dark:bg-secondary-900 text-secondary dark:text-secondary-300 hover:bg-secondary/20 transition-all flex items-center"
          @click="togglePreviewMode"
        >
          <i :class="`fa fa-${previewMode === 'text' ? 'cube' : 'font'} mr-1`"></i>
          {{ previewMode === 'text' ? '切换到元素预览' : '切换到文本预览' }}
        </button>
      </div>
    </div>
    
    <div class="mb-4">
      <div class="flex items-center mb-2">
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200">
          Tailwind 类名
          <span class="ml-2 text-xs text-neutral-500 dark:text-neutral-400">
            ({{ tailwindClassList.filter(i => i.type === 'tw').length }} 个有效类)
          </span>
        </label>
        <div class="ml-auto text-xs text-neutral-500 dark:text-neutral-400 flex items-center">
          <span v-if="disabledClasses.length > 0" class="mr-2">
            已禁用 {{ disabledClasses.length }} 个类
          </span>
          <button 
            v-if="disabledClasses.length > 0" 
            @click="resetDisabledClasses" 
            class="text-primary dark:text-primary-400 hover:underline"
          >
            重置
          </button>
        </div>
      </div>
      
      <div class="w-full min-h-36 max-h-48 overflow-y-auto scrollbar-hide bg-neutral-50 dark:bg-neutral-900 border border-primary-100 dark:border-primary-800 rounded-lg p-3 font-mono text-base break-words">
        <span
          v-for="(item, idx) in tailwindClassList"
          :key="idx"
          :class="[
            'inline-flex items-center mr-2 mb-2 px-2 py-1 rounded transition-all duration-300 relative',
            item.type === 'tw' 
              ? disabledClasses.includes(idx)
                ? 'text-neutral-400 dark:text-neutral-600 bg-neutral-100 dark:bg-neutral-800 border border-dashed border-neutral-300 dark:border-neutral-700 line-through cursor-pointer'
                : 'text-primary-700 dark:text-primary-200 bg-primary-50 dark:bg-primary-900/50 hover:bg-primary-100 dark:hover:bg-primary-800/70 cursor-pointer'
              : 'bg-red-100 dark:bg-red-900/50 text-red-500 hover:bg-red-200 dark:hover:bg-red-800/70 cursor-pointer'
          ]"
          :title="item.type === 'tw' ? (disabledClasses.includes(idx) ? '点击启用此类' : '点击禁用此类') : '未识别的CSS属性'"
          @click="toggleClass(idx)"
        >
          {{ item.value }}
          <i 
            v-if="item.type === 'tw'" 
            :class="[
              'ml-1',
              disabledClasses.includes(idx) 
                ? 'fa fa-plus-circle text-neutral-400 dark:text-neutral-600' 
                : 'fa fa-times-circle text-primary-400 dark:text-primary-600 opacity-50 hover:opacity-100'
            ]"
          ></i>
        </span>
        <div v-if="tailwindClassList.length === 0" class="text-neutral-400 dark:text-neutral-500 text-center py-4">
          暂无 Tailwind 类
        </div>
      </div>
      
      <!-- 类操作说明 -->
      <div class="mt-2 text-xs text-neutral-500 dark:text-neutral-400 flex items-center">
        <i class="fa fa-info-circle mr-1"></i>
        <span>点击类名可以启用/禁用该类，查看效果变化</span>
      </div>
      
      <div v-if="copied" class="text-green-500 text-xs mt-2 flex items-center">
        <i class="fa fa-check-circle mr-1"></i> 已复制到剪贴板！
      </div>
    </div>
    
    <div class="flex-1 flex flex-col">
      <div class="flex items-center justify-between mb-2">
        <label class="block text-sm font-medium text-neutral-700 dark:text-neutral-200">实时预览</label>
        <div class="flex items-center">
          <button
            v-if="previewMode === 'text'"
            class="text-xs px-2 py-1 rounded bg-neutral-100 dark:bg-neutral-700 text-neutral-700 dark:text-neutral-200 hover:bg-neutral-200 dark:hover:bg-neutral-600 transition-all flex items-center"
            @click="editPreviewText = true"
          >
            <i class="fa fa-pencil mr-1"></i>
            编辑文本
          </button>
        </div>
      </div>
      
      <!-- 预览区域 -->
      <div class="flex-1 flex items-center justify-center min-h-32 bg-neutral-50 dark:bg-neutral-900 rounded-lg border border-dashed border-neutral-300 dark:border-neutral-700 p-4 overflow-hidden">
        <!-- 文本预览模式 -->
        <div v-if="previewMode === 'text'" class="w-full h-full flex items-center justify-center">
          <div 
            v-if="!editPreviewText"
            :class="tailwindClass"
            class="transition-all duration-300 px-6 py-2 rounded-lg border border-dashed border-primary-200 dark:border-primary-700 bg-white dark:bg-neutral-900 text-base font-semibold text-primary dark:text-primary-300 shadow max-w-full break-words text-center"
            @dblclick="editPreviewText = true"
          >
            {{ customText || 'Tailwind 预览文本' }}
          </div>
          
          <input 
            v-else
            v-model="previewTextValue"
            class="w-full max-w-md px-4 py-2 border border-primary-300 dark:border-primary-700 rounded-lg bg-white dark:bg-neutral-800 text-neutral-800 dark:text-neutral-200 focus:outline-none focus:ring-2 focus:ring-primary/30"
            @blur="savePreviewText"
            @keyup.enter="savePreviewText"
            ref="previewTextInput"
          />
        </div>
        
        <!-- 元素预览模式 -->
        <div v-else class="w-full h-full flex flex-col items-center justify-center gap-4">
          <!-- 按钮预览 -->
          <button :class="['px-4 py-2 rounded-lg transition-all', tailwindClass]">
            按钮预览
          </button>
          
          <!-- 卡片预览 -->
          <div :class="['p-4 rounded-lg transition-all max-w-sm w-full', tailwindClass]">
            <div class="text-lg font-semibold mb-2">卡片标题</div>
            <div class="text-sm">这是一个应用了当前Tailwind类的卡片元素预览。</div>
          </div>
          
          <!-- 标签预览 -->
          <div :class="['inline-block px-3 py-1 rounded-full transition-all', tailwindClass]">
            标签预览
          </div>
        </div>
      </div>
    </div>
    
    <!-- Toast提示组件 -->
    <transition name="toast">
      <div 
        v-if="toast.show" 
        class="absolute bottom-4 right-4 px-4 py-2 rounded-lg shadow-lg flex items-center animate-slide-down"
        :class="{
          'bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-300': toast.type === 'success',
          'bg-blue-100 dark:bg-blue-900 text-blue-700 dark:text-blue-300': toast.type === 'info',
          'bg-red-100 dark:bg-red-900 text-red-700 dark:text-red-300': toast.type === 'error'
        }"
      >
        <i 
          class="mr-2"
          :class="{
            'fa fa-check-circle': toast.type === 'success',
            'fa fa-info-circle': toast.type === 'info',
            'fa fa-exclamation-circle': toast.type === 'error'
          }"
        ></i>
        {{ toast.message }}
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'TailwindPreview',
  props: {
    tailwindClassList: {
      type: Array,
      required: true
    },
    customText: {
      type: String,
      default: 'Tailwind 预览文本'
    }
  },
  emits: ['update:customText'],
  data() {
    return {
      copied: false,
      previewMode: 'text', // 'text' 或 'element'
      editPreviewText: false,
      previewTextValue: '',
      disabledClasses: [],
      toast: {
        show: false,
        message: '',
        type: 'info', // 'success', 'info', 'error'
        timeout: null
      }
    };
  },
  computed: {
    tailwindClass() {
      return this.tailwindClassList
        .filter((item, index) => item.type === 'tw' && !this.disabledClasses.includes(index))
        .map(i => i.value)
        .join(' ');
    }
  },
  watch: {
    customText: {
      immediate: true,
      handler(val) {
        this.previewTextValue = val;
      }
    }
  },
  methods: {
    copyTailwind() {
      const text = this.tailwindClass;
      if (!text) return;
      
      navigator.clipboard.writeText(text).then(() => {
        this.copied = true;
        setTimeout(() => { this.copied = false; }, 1200);
        this.showToast('已复制到剪贴板', 'success');
      });
    },
    togglePreviewMode() {
      this.previewMode = this.previewMode === 'text' ? 'element' : 'text';
    },
    savePreviewText() {
      this.$emit('update:customText', this.previewTextValue);
      this.editPreviewText = false;
    },
    toggleClass(index) {
      const idx = this.disabledClasses.indexOf(index);
      if (idx > -1) {
        // 如果已经禁用，则启用
        this.disabledClasses.splice(idx, 1);
        // 显示启用提示
        this.showToast(`已启用: ${this.tailwindClassList[index].value}`, 'success');
      } else {
        // 如果未禁用，则禁用
        this.disabledClasses.push(index);
        // 显示禁用提示
        this.showToast(`已禁用: ${this.tailwindClassList[index].value}`, 'info');
      }
    },
    resetDisabledClasses() {
      this.disabledClasses = [];
      this.showToast('已重置所有类', 'success');
    },
    showToast(message, type = 'info') {
      // 清除之前的定时器
      if (this.toast.timeout) {
        clearTimeout(this.toast.timeout);
      }
      
      // 设置新的提示
      this.toast.show = true;
      this.toast.message = message;
      this.toast.type = type;
      
      // 设置自动隐藏
      this.toast.timeout = setTimeout(() => {
        this.toast.show = false;
      }, 2000);
    }
  },
  mounted() {
    this.$watch('editPreviewText', (val) => {
      if (val) {
        this.$nextTick(() => {
          if (this.$refs.previewTextInput) {
            this.$refs.previewTextInput.focus();
          }
        });
      }
    });
  },
  beforeDestroy() {
    // 清除定时器
    if (this.toast.timeout) {
      clearTimeout(this.toast.timeout);
    }
  }
};
</script>

<style scoped>
.toast-enter-active, .toast-leave-active {
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.toast-enter, .toast-leave-to {
  transform: translateY(20px);
  opacity: 0;
}
</style> 