// CSS 到 Tailwind 的映射配置
export const defaultSample = `display: flex;
flex-direction: row;
justify-content: flex-start;
align-items: center;
color: #f00;
font-size: 20px;
padding: 8px 16px;
border-radius: 8px;
box-shadow: 0 2px 8px #0002;
width: 320px;
height: 60px;
margin: 12px 24px 8px 4px;
background: #f5f5f5;
text-align: center;
text-decoration: underline;
cursor: pointer;`;

// CSS 到 Tailwind 的映射规则
export const css2twMap = [
    // Flex/Grid 布局
    [/display:\s*flex;?/i, () => 'flex'],
    [/display:\s*inline-flex;?/i, () => 'inline-flex'],
    [/display:\s*grid;?/i, () => 'grid'],
    [/display:\s*inline-grid;?/i, () => 'inline-grid'],
    [/flex-direction:\s*row;?/i, () => 'flex-row'],
    [/flex-direction:\s*column;?/i, () => 'flex-col'],
    [/flex-wrap:\s*wrap;?/i, () => 'flex-wrap'],
    [/flex-wrap:\s*nowrap;?/i, () => 'flex-nowrap'],
    [/gap:\s*([0-9.]+)px;?/i, (m) => `gap-[${m[1]}px]`],
    [/justify-content:\s*flex-start;?/i, () => 'justify-start'],
    [/justify-content:\s*flex-end;?/i, () => 'justify-end'],
    [/justify-content:\s*center;?/i, () => 'justify-center'],
    [/justify-content:\s*space-between;?/i, () => 'justify-between'],
    [/justify-content:\s*space-around;?/i, () => 'justify-around'],
    [/justify-content:\s*space-evenly;?/i, () => 'justify-evenly'],
    [/align-items:\s*flex-start;?/i, () => 'items-start'],
    [/align-items:\s*flex-end;?/i, () => 'items-end'],
    [/align-items:\s*center;?/i, () => 'items-center'],
    [/align-items:\s*baseline;?/i, () => 'items-baseline'],
    [/align-items:\s*stretch;?/i, () => 'items-stretch'],
    // 颜色
    [/color:\s*#?([0-9a-fA-F]{3,6});?/i, (m) => `text-[${m[1].startsWith('#') ? m[1] : '#' + m[1]}]`],
    [/color:\s*([^;]+);?/i, (m) => `text-[${m[1].trim()}]`],
    [/background-color:\s*#?([0-9a-fA-F]{3,6});?/i, (m) => `bg-[${m[1].startsWith('#') ? m[1] : '#' + m[1]}]`],
    [/background-color:\s*([^;]+);?/i, (m) => `bg-[${m[1].trim()}]`],
    [/background:\s*#?([0-9a-fA-F]{3,6});?/i, (m) => `bg-[${m[1].startsWith('#') ? m[1] : '#' + m[1]}]`],
    [/background:\s*([^;]+);?/i, (m) => {
        // 确保我们获取的是颜色值而不是其他背景属性
        const value = m[1].trim();
        if (value.includes('url') || value.includes('linear-gradient') || value.includes('radial-gradient')) {
            return `/* 复杂背景: ${value} */`;
        }
        return `bg-[${value}]`;
    }],
    // 字体
    [/font-size:\s*([0-9.]+)px;?/i, (m) => `text-[${m[1]}px]`],
    [/font-weight:\s*bold;?/i, () => 'font-bold'],
    [/font-weight:\s*([0-9]{3});?/i, (m) => `font-[${m[1]}]`],
    [/font-style:\s*italic;?/i, () => 'italic'],
    [/font-style:\s*normal;?/i, () => 'not-italic'],
    // 间距（支持4值/3值/2值/1值，顺序：上 右 下 左）
    [/padding:\s*([0-9.]+)px\s+([0-9.]+)px\s+([0-9.]+)px\s+([0-9.]+)px;?/i, (m) =>
        `pt-[${m[1]}px] pr-[${m[2]}px] pb-[${m[3]}px] pl-[${m[4]}px]`
    ],
    [/padding:\s*([0-9.]+)px\s+([0-9.]+)px\s+([0-9.]+)px;?/i, (m) =>
        `pt-[${m[1]}px] px-[${m[2]}px] pb-[${m[3]}px]`
    ],
    [/padding:\s*([0-9.]+)px\s+([0-9.]+)px;?/i, (m) =>
        `py-[${m[1]}px] px-[${m[2]}px]`
    ],
    [/padding:\s*([0-9.]+)px;?/i, (m) =>
        `p-[${m[1]}px]`
    ],
    [/padding-(top|right|bottom|left):\s*([0-9.]+)px;?/i, (m) => {
        const map = { top: 't', right: 'r', bottom: 'b', left: 'l' }; return `p${map[m[1]]}-[${m[2]}px]`;
    }],
    [/margin:\s*([0-9.]+)px\s+([0-9.]+)px\s+([0-9.]+)px\s+([0-9.]+)px;?/i, (m) =>
        `mt-[${m[1]}px] mr-[${m[2]}px] mb-[${m[3]}px] ml-[${m[4]}px]`
    ],
    [/margin:\s*([0-9.]+)px\s+([0-9.]+)px\s+([0-9.]+)px;?/i, (m) =>
        `mt-[${m[1]}px] mx-[${m[2]}px] mb-[${m[3]}px]`
    ],
    [/margin:\s*([0-9.]+)px\s+([0-9.]+)px;?/i, (m) =>
        `my-[${m[1]}px] mx-[${m[2]}px]`
    ],
    [/margin:\s*([0-9.]+)px;?/i, (m) =>
        `m-[${m[1]}px]`
    ],
    [/margin-(top|right|bottom|left):\s*([0-9.]+)px;?/i, (m) => {
        const map = { top: 't', right: 'r', bottom: 'b', left: 'l' }; return `m${map[m[1]]}-[${m[2]}px]`;
    }],
    // 圆角
    [/border-radius:\s*([0-9.]+)px;?/i, (m) => `rounded-[${m[1]}px]`],
    [/border-radius:\s*50%;?/i, () => 'rounded-full'],
    // 边框
    [/border:\s*([0-9.]+)px\s+solid\s+([^;]+);?/i, (m) => `border border-[${m[2].trim()}] border-[${m[1]}px]`],
    [/border-width:\s*([0-9.]+)px;?/i, (m) => `border-[${m[1]}px]`],
    [/border-color:\s*([^;]+);?/i, (m) => `border-[${m[1].trim()}]`],
    // 阴影
    [/box-shadow:\s*none;?/i, () => 'shadow-none'],
    [/box-shadow:\s*0\s+1px\s+2px.*;/i, () => 'shadow-sm'],
    [/box-shadow:\s*0\s+4px\s+6px.*;/i, () => 'shadow-md'],
    [/box-shadow:\s*0\s+10px\s+15px.*;/i, () => 'shadow-lg'],
    [/box-shadow:\s*0\s+20px\s+25px.*;/i, () => 'shadow-xl'],
    [/box-shadow:/i, () => 'shadow'],
    // 宽高
    [/width:\s*([0-9.]+)px;?/i, (m) => `w-[${m[1]}px]`],
    [/height:\s*([0-9.]+)px;?/i, (m) => `h-[${m[1]}px]`],
    // 行高
    [/line-height:\s*([0-9.]+);?/i, (m) => `leading-[${m[1]}]`],
    // 透明度
    [/opacity:\s*([0-9.]+);?/i, (m) => `opacity-[${m[1]}]`],
    // 文字对齐
    [/text-align:\s*center;?/i, () => 'text-center'],
    [/text-align:\s*right;?/i, () => 'text-right'],
    [/text-align:\s*left;?/i, () => 'text-left'],
    // 装饰
    [/text-decoration\s*:\s*underline;?/i, () => 'underline'],
    [/text-decoration\s*:\s*line-through;?/i, () => 'line-through'],
    [/text-decoration\s*:\s*none;?/i, () => 'no-underline'],
    // 鼠标指针
    [/cursor\s*:\s*pointer;?/i, () => 'cursor-pointer'],
    [/cursor\s*:\s*not-allowed;?/i, () => 'cursor-not-allowed'],
];

