/**
 * 将CSS转换为Tailwind类
 * @param {string} css CSS样式字符串
 * @returns {Array} 转换后的Tailwind类数组
 */
export function css2tw(css) {
    if (!css) return [];
    let tw = [];
    
    // 预处理CSS，确保每个声明都以分号结尾
    const normalizedCss = css.trim().endsWith(';') ? css : css + ';';
    
    // 将CSS分割为单独的声明
    const declarations = normalizedCss.split(';').filter(d => d.trim());
    
    for (const declaration of declarations) {
        let matched = false;
        const line = declaration.trim();
        
        if (!line) continue;
        
        // 特殊处理background-color属性
        if (line.match(/^background-color:/i)) {
            const colorMatch = line.match(/background-color:\s*([^;]+)/i);
            if (colorMatch && colorMatch[1]) {
                const colorValue = colorMatch[1].trim();
                tw.push({ type: 'tw', value: `bg-[${colorValue}]` });
                matched = true;
                continue;
            }
        }
        
        // 特殊处理background属性
        if (line.match(/^background:/i)) {
            const colorMatch = line.match(/background:\s*([^;]+)/i);
            if (colorMatch && colorMatch[1]) {
                const colorValue = colorMatch[1].trim();
                // 如果是纯色值，转换为bg-[]
                if (colorValue.match(/^#[0-9a-fA-F]{3,8}$/) || 
                    colorValue.match(/^[a-z]+$/) || 
                    colorValue.match(/^rgb\(/) || 
                    colorValue.match(/^rgba\(/)) {
                    tw.push({ type: 'tw', value: `bg-[${colorValue}]` });
                    matched = true;
                    continue;
                }
            }
        }
        
        // 使用常规映射规则
        if (!matched) {
            for (const [reg, fn] of css2twMap) {
                const m = (line + ';').match(reg);
                if (m) {
                    tw.push({ type: 'tw', value: fn(m) });
                    matched = true;
                    break;
                }
            }
        }
        
        // 未识别的CSS属性
        if (!matched) {
            tw.push({ type: 'raw', value: line.replace(/;$/, '') });
        }
    }
    
    return tw;
}

/**
 * 将Tailwind类转换为CSS
 * @param {string} tw Tailwind类字符串
 * @returns {string} 转换后的CSS样式
 */
export function tailwind2css(tw) {
    if (!tw) return '';
    return tw
        .split(/\s+/)
        .map(cls => {
            for (const rule of tw2cssMap) {
                if (typeof rule[0] === 'string') {
                    if (cls === rule[0]) return rule[1];
                } else if (rule[0] instanceof RegExp) {
                    const m = cls.match(rule[0]);
                    if (m) return typeof rule[1] === 'function' ? rule[1](m) : rule[1];
                }
            }
            return `/* 未识别: ${cls} */`;
        })
        .join(' ');
} 